import React from "react";

import styled from "styled-components";

const Cell = props => {
  let handleClick = () =>{
    props.changer(props.row, props.col)
  }
  let style= {
    width: "80px",
    height: "80px",
    background: props.status ? "#38a1e6": "#797979",
    margin: "5px",
    boxShadow: props.status ? "0px 0px 6px 4px #00fffa33,  1px 2px 6px 1px #aeaeaeae" : "1px 2px 6px 1px #aeaeaeae",
    transition: "all 0.3s ease-in-out",
    border: "none",
    borderRadius: "5px"
  }
  return(
    <StyledCell status = {props.status} onClick = {handleClick}> </StyledCell>
  )
}

let StyledCell = styled.div`
flex: 1 1 20%;
justify-content: center;
background: ${props => props.status ? "#38a1e6": "#797979"}
margin: 5px;
box-shadow: ${props => props.status ? "0px 0px 6px 4px #00fffa33,  1px 2px 6px 1px #aeaeaeae" : "1px 2px 6px 1px #aeaeaeae"}
transition: all 0.3s ease-in-out;
border: none;
border-radius: 5px;
&:before {
  content:'';
     float:left;
     padding-top:100%;
}


`

export default Cell
