import React, {Component} from "react";
import Cell from "./Cell";
import styled from "styled-components";
class CellRow extends Component {
  constructor(props){
    super(props)
  }
  render(){
    let style= {
      display: "flex",
      justifyContent: "center"
    }
    let cells = this.props.cells.map((c, i) => {
      return <Cell status={c} changer = {this.props.changer} row = {this.props.row} col={i}/>
    })
    return(
      <StyledRow style={style}>
      {cells}
      </StyledRow>
    )
  }
}


let StyledRow = styled.div`
  display: flex;
  width: 100%;
  justify-content: center;

`

export default CellRow
