import React from "react";
import Button from "./Button";
import { Transition ,TransitionGroup, animateOnEnter, animateOnExit } from 'react-transition-group';
const WinBanner = props => {
  const fadeIn = `@keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
`
  const style = {
    width: "100vw",
    height: "100vh",
    background: "#61dafbe0",
    zIndex: "10",
    position: "absolute",
    left: "0",
    bottom: "0",
    right: "0",
    top:"0",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    opacity: props.win ? "1" : "0",
    transition: `opacity 300ms ease-in-out`,
    animation: `fadeIn 150ms ease-in`
  }

  const transitionStyles = {
  entering: { opacity: 1 },
  entered:  { opacity: 1 },
  exiting:  { opacity: 0 },
  exited:  { opacity: 0 },
};
  return(
      <div style ={style}>
        <h2>Congratulations!</h2>
        <Button onClick={props.newGame} > Play again? </Button>
      </div>
  )
}

export default WinBanner
