import React, {Component} from "react";

const Button = props => {
  let style = {
    width: "40%",
    height: "60px",
    alignSelf: "center",
    margin: "4%",
    fontSize: "1.5rem",
    background: "#9c6ccc",
    border: "1px solid grey",
    color: "white",
    borderRadius: "5px",
    boxShadow:  "1px 2px 6px 1px #9c6cccae"

    }
  return(
    <button style={style} onClick={props.onClick}>{props.children}</button>
  )
}

export default Button
